<template>
  <div class="comp-bargain-list">
    <div class="comp-bargain-list-banner"></div>
    <div class="comp-bargain-list-container"
      v-if="sellerGoods.length">
      <div class="comp-bargain-list-item"
        v-for="(item, index) in sellerGoods"
        :key="index">
        <div class="comp-bargain-list-item-image" :style="{ 'background-image': 'url(' + item.goods_img + ')' }"></div>
        <div class="comp-bargain-list-item-title">{{ item.coupon_title }}</div>
        <div class="comp-bargain-list-item-tool">
          <span>¥{{ item.goods_price }}</span>
          <button class="primate-btn" @click="addGoodsToCut(item.id)">立即砍价</button>
        </div>
      </div>
    </div>
    <div class="comp-bargain-list-empty" v-else>暂无数据</div>
  </div>
</template>

<script>
export default {
  name: 'compBargainList',

  props: {
    seller_id: '',
    activity_id: '',
    car_series_id: ''
  },

  computed: {
    sellerGoods () {
      return this.$store.state.product.sellerGoods
    },

    userid () {
      return this.$store.state.user.userid
    },

    token () {
      return this.$store.state.user.token
    }
  },

  methods: {
    getSellerGoods () {
      this.$store.dispatch('product/getSellerGoods', {
        seller_id: this.seller_id,
        activity_id: this.activity_id,
        car_series_id: this.car_series_id
      })
        .then(res => {
          console.log(res)
        })
        .catch(err => {
          console.log(err)
        })
    },

    addGoodsToCut (goodsId) {
      this.$store.dispatch('product/addGoodsToCut', {
        goods_id: goodsId,
        activity_id: this.activity_id,
        user_id: this.userid,
        token: this.token
      })
        .then(cutId => {
          console.log(cutId)
          this.$emit('onAddGoodsToCut', cutId)
        })
        .catch(err => {
          console.log(err)
        })
    }
  },

  created () {
    this.getSellerGoods()
  }
}
</script>

