<template>
  <dialogs class="dialogs-signup">
    <div class="dialogs-signup-bg">
      <h2 class="dialogs-signup-title">离砍价还差一步<br />请先留下大名</h2>
      <div class="dialogs-signup-main">
        <div class="dialogs-signup-cell">
          <div class="dialogs-signup-cell-label">姓名：</div>
          <div class="dialogs-signup-cell-input">
            <input type="text" v-model="username">
          </div>
        </div>
        <div class="dialogs-signup-cell">
          <div class="dialogs-signup-cell-label">手机号码：</div>
          <div class="dialogs-signup-cell-input">
            <input type="text" maxlength="11" v-model="phone">
          </div>
        </div>
        <div class="dialogs-signup-cell dialogs-signup-cell-code">
          <div class="dialogs-signup-cell-label">验证码：</div>
          <div class="dialogs-signup-cell-input">
            <input type="text" v-model="code">
            <button class="dialogs-signup-cell-input-btn" :disabled="isSendPhoneCodeDisabled" @click="sendPhoneCode">{{ sendPhoneCodeText }}</button>
          </div>
        </div>
        <div class="dialogs-signup-cell">
          <div class="dialogs-signup-cell-label">车系：</div>
          <div class="dialogs-signup-cell-input">
            <select v-model="car_series_id">
              <option value="" selected>请选择车系</option>
              <option 
                v-for="(opt, index) in carSeries"
                :key="index"
                :value="opt.gc_id">{{ opt.gc_name }}</option>
            </select>
          </div>
        </div>

        <div class="dialogs-signup-tip">{{ tip }}</div>
      </div>
      <div class="dialogs-signup-submit">
        <button class="primate-btn" :disabled="isSubmitDisabled" @click="submit">立即砍价</button>
      </div>
    </div>
  </dialogs>
</template>

<script>
import dialogs from '../dialogs/index'

export default {
  name: 'dialogsSignup',

  components: {
    dialogs
  },

  props: {
    seller_id: '',
    wx_data: {
      type: Object,
      default () {
        return {}
      }
    }
  },

  data () {
    return {
      username: '',
      phone: '',
      code: '',
      car_series_id: '',

      isSubmitDisabled: false,
      isSendPhoneCodeDisabled: false,
      sendPhoneCodeSecond: 60,
      sendPhoneCodeCountDown: 60,
      sendPhoneCodeTimer: null,
      tip: ''
    }
  },

  computed: {
    carSeries () {
      return this.$store.state.product.carSeries
    },

    sendPhoneCodeText () {
      return this.isSendPhoneCodeDisabled ? this.sendPhoneCodeCountDown + 's' : '获取验证码'
    }
  },

  methods: {
    sendPhoneCode () {
      if (this.isSendPhoneCodeDisabled) {
        return
      }

      if (!/^[0-9]{11}$/.test(this.phone)) {
        this.tip = '手机号码错误'
        return
      }

      this.isSendPhoneCodeDisabled = true

      this.$store.dispatch('user/sendPhoneCode', this.phone)
        .then(() => {
          this.countDown()
        })
        .catch(() => {
          this.isSendPhoneCodeDisabled = false
        })
    },

    countDown () {
      this.sendPhoneCodeTimer = setInterval(() => {
        this.sendPhoneCodeCountDown -= 1
        if (this.sendPhoneCodeCountDown <= 0) {
          this.isSendPhoneCodeDisabled = false
          this.sendPhoneCodeCountDown = this.sendPhoneCodeSecond
          clearInterval(this.sendPhoneCodeTimer)
        }
      }, 1000)
    },

    validate () {
      if (!this.seller_id) {
        this.tip = '缺少参数seller_id'
        return false
      }

      if (!this.wx_data.wx_name || !this.wx_data.wx_openid || !this.wx_data.wx_head_img_url) {
        this.tip = '缺少微信用户数据'
        return false
      }

      if (!this.username) {
        this.tip = '姓名不能为空'
        return false
      }

      if (!/^[0-9]{11}$/.test(this.phone)) {
        this.tip = '手机号码错误'
        return false
      }

      if (!this.code) {
        this.tip = '手机验证码不能为空'
        return false
      }

      if (!this.car_series_id) {
        this.tip = '请选择车系'
        return false
      }

      this.tip = ''
      return true
    },

    submit () {
      if (this.isSubmitDisabled) {
        return
      }
      
      if (!this.validate()) {
        return
      }

      this.isSubmitDisabled = true
      this.tip = '正在提交，请稍后'

      this.$store.dispatch('user/adduser', {
        username: this.username,
        phone: this.phone,
        code: this.code,
        car_series_id: this.car_series_id,
        seller_id: this.seller_id,
        wx_name: this.wx_data.wx_name,
        wx_openid: this.wx_data.wx_openid,
        wx_head_img_url: this.wx_data.wx_head_img_url
      })
        .then(res => {
          this.$emit('signupSuccess', { token: res.token, car_series_id: this.car_series_id })
          this.isSubmitDisabled = false
          this.tip = ''
        })
        .catch(err => {
          this.isSubmitDisabled = false
          if (err.data && err.data.msg) {
            this.tip = err.data.msg
          } else {
            this.tip = '提交失败，请重试'
          }
        })
    }
  }
}
</script>

