        color: #000000;
        background: transparent;
        text-indent: 4px;
      }
    }

    &-code {
      .dialogs-signup-cell-input {
        overflow: hidden;

        input {
          float: left;
          width: 50%;
        }

        &-btn {
          float: right;
          width: 46%;
          height: 100%;
          text-align: center;
          line-height: 72px;
          font-size: 20px;
          border-radius: 4px;
          background: #ffa4b6;
