<template>
  <div id="app">

    <header-bar class="header-bar"></header-bar>

    <div class="home-btn-container" v-show="!currentComp">
      <div class="home-btn" @click="bargain">立即砍价</div>
    </div>

    <transition name="fade">
      <dialogs-signup
        v-if="isShowSignup"
        :seller_id="seller_id"
        :wx_data="wx_data"
        @signupSuccess="signupSuccess"></dialogs-signup>
    </transition>

    <transition name="fade">
      <component
        :is="currentComp"
        :seller_id="seller_id"
        :activity_id="activity_id"
        :car_series_id="car_series_id"
        :cut_id="currentCutId"
        :wx_data="wx_data"
        @onAddGoodsToCut="onAddGoodsToCut"
        @gotoList="gotoList"></component>
    </transition>

  </div>
</template>

<script>
import headerBar from '@/modules/header-bar/index'
import dialogsSignup from './modules/dialogsSignup/index'
import compBargainList from './modules/compBargainList/index'
import compBargainDetail from './modules/compBargainDetail/index'
import utils from '@/utils/index'

export default {
  name: 'app',

  components: {
    headerBar,
    dialogsSignup,
    compBargainList,
    compBargainDetail
  },

  computed: {
    token () {
      return this.$store.state.user.token
    }
  },

  data () {
    return {
      isShowSignup: false,
      currentComp: '',
      currentCutId: '',
      seller_id: utils.getParam('seller_id'),
      activity_id: utils.getParam('activity_id'),
      car_series_id: utils.getStorage('car_series_id'),

      wx_data: {
        wx_name: '飞行家',
        wx_openid: 'test',
        wx_head_img_url: 'https://puui.qpic.cn/tv/0/37883357/0'
      }
    }
  },

  methods: {
    bargain () {
      if (this.token) {
        this.isShowSignup = false
        this.currentComp = 'compBargainList'
      } else {
        this.isShowSignup = true;
      }
    },

    getCarSeries () {
      this.$store.dispatch('product/getCarSeries')
    },

    signupSuccess ({ car_series_id }) {
      this.isShowSignup = false
      this.currentComp = 'compBargainList'
      this.car_series_id = car_series_id
      utils.setStorage('car_series_id', car_series_id)
    },

    onAddGoodsToCut (cut_id) {
      this.currentCutId = cut_id
      this.currentComp = 'compBargainDetail'
    },

    gotoList () {
      this.currentComp = 'compBargainList'
    },

    getWxShareInfo () {
        let host = window.location.href || window.location.href
        this.$store.dispatch('wx/getWxShareInfo', encodeURIComponent(host))
        .then(res => {
          console.log(res)
          this.wxConfig(res)
          this.wxReady()
        })
        .catch(err => {
          console.log(err)
        })
    },

    wxConfig (share_info) {

      try {
          console.log(share_info);
        window.wx.config({
          debug: false,
          appId: share_info.data.appId,
          timestamp: share_info.data.timestamp,
          nonceStr: share_info.data.nonceStr,
          signature: share_info.data.signature,
          jsApiList: [
            'onMenuShareTimeline',
            'onMenuShareAppMessage',
            'onMenuShareQQ',
            'onMenuShareWeibo',
            'onMenuShareQZone'
          ]
        })
      } catch (ex) {
        console.log(ex)
      }
    },

    wxReady () {
      try {
        window.wx.ready(() => {
          console.log('wx.ready')
        })
      } catch (ex) {
        console.log(ex)
      }
    }
  },

  created () {
    this.getCarSeries()

    this.getWxShareInfo();
  },

  mounted () {
    let wx_result = window.wx_result
    if (typeof wx_result === 'object') {
      this.wx_data.wx_name = wx_result.nickname
      this.wx_data.wx_openid = wx_result.openid
      this.wx_data.wx_head_img_url = wx_result.headimgurl
    }
  }
}
</script>

<style lang="less">
@import '~@/less/public.less';

