  position: relative;
  height: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fff url(./images/kv.png) 50% 48px / contain no-repeat;
}

.header-bar {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.home-btn-container {
  position: absolute;
  top: 78%;
  left: 0;
  width: 100%;
  padding: 0 50px;
  box-sizing: border-box;

  .home-btn {
    margin: 0 auto;
    width: 48%;
    line-height: 72px;
    text-align: center;
    font-size: 34px;
    color: #fff;
    background: #c90028;
    border-radius: 6px;
    transition: all 0.1s linear;

    &:hover,
    &:active {
      box-shadow: 8px 8px 4px rgba(0, 0, 0, .5);
    }
  }
}

.fade-enter-active {
  animation: fade-in .2s linear;
}
.fade-leave-active {
  animation: fade-in .2s linear reverse;
}
@keyframes fade-in {
  0% {
    transform: scale(1.1, 1.1);
    opacity: 0;
  }
  100% {
    transform: scale(1, 1);
    opacity: 1;
  }
}
</style>
